import { Button } from '@material-ui/core';
import _get from 'lodash.get';
import { useMutation, gql } from '@apollo/client';
import _isEmpty from 'lodash.isempty';
import { approveProperty } from '../../../../graphql/mutations';
import { TEditProperty } from '../../../../types';
import { useSnackBar } from '../../../../hooks';
import { isApproved } from '../Rules';

type Props = {
	property: TEditProperty;
	refetch: Function;
};

const ApproveAction = (props: Props) => {
	const { property, refetch } = props;
	const propertyId = _get(property, 'PropertyId', '');
	const propertyData = _get(property, 'PropertyData');
	const statusId = _get(property, 'StatusId');
	const listPriceRange = _get(property, 'ListPriceRange', {});
	const max = _get(listPriceRange, 'Max', 0);
	const min = _get(listPriceRange, 'Min', 0);
	const yearBuilt =
		_get(propertyData, 'YearBuilt', '') ||
		_get(propertyData, 'YearBuiltOverride', '');
	const vestingType =
		_get(propertyData, 'VestingTypeMapped', '') ||
		_get(propertyData, 'VestingTypeOverride', '');

	const isHideButton =
		isApproved(statusId) ||
		_isEmpty(propertyData) ||
		max <= 0 ||
		_isEmpty(vestingType) ||
		min <= 0 ||
		yearBuilt <= 0;

	const showSnackBar = useSnackBar();
	const [approveAction] = useMutation(gql(approveProperty));

	const approveHandler = async () => {
		try {
			await approveAction({
				variables: {
					PropertyId: propertyId,
				},
			});
			showSnackBar({
				message: 'Property has approved',
				variant: 'alert',
				alertSeverity: 'success',
			});
			refetch();
		} catch (e) {
			const message = _get(e, 'message', 'Something went wrong');
			showSnackBar({
				message,
			});
		}
	};

	if (isHideButton) return <></>;

	return (
		<Button
			onClick={approveHandler}
			type='button'
			variant='outlined'
			color='secondary'
		>
			Approve
		</Button>
	);
};

export default ApproveAction;
