import {
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableHead,
	TableRow,
	Button,
	Alert,
	useTheme,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { useState, useEffect } from 'react';
import _isEmpty from 'lodash.isempty';
import { PropertyData } from '../../../../types';
import Loader from '../../../../ui-component/Loader';

type Props = {
	properties: PropertyData[];
	loading: boolean;
	onSelect: (property: PropertyData) => void;
	isSearchNotFound: boolean;
};

export interface ColumnProps {
	id: string;
	label: string;
	align?: 'right' | 'left' | 'inherit' | 'center' | 'justify' | undefined;
	format?: (value: Date | number) => string | boolean;
}
const columns: ColumnProps[] = [
	{
		id: 'Address',
		label: 'Address',
		align: 'left',
	},
	{
		id: 'Owner',
		label: 'Owner',
		align: 'left',
	},
	{
		id: 'Action',
		label: 'Action',
		align: 'left',
	},
];

// style constant
const useStyles = makeStyles(theme => ({
	root: {
		width: '100%',
		overflow: 'hidden',
	},
	container: {
		maxHeight: 440,
	},
	notFoundMessage: {
		margin: `${theme.spacing(2)} 0 ${theme.spacing(2)} 0`,
	},
}));

const SearchPropertyList = (props: Props) => {
	const { properties, loading, onSelect, isSearchNotFound } = props;
	const [isHidden, setIsHidden] = useState(false);
	const classes = useStyles();
	const theme = useTheme();

	const onSelectHandler = (row: PropertyData) => {
		onSelect(row);
		setIsHidden(true);
	};

	useEffect(() => {
		setIsHidden(loading);
	}, [loading]);

	if (loading) return <Loader />;
	if (isSearchNotFound)
		return (
			<Alert
				className={classes.notFoundMessage}
				severity='error'
				sx={{ color: theme.palette.error.main }}
			>
				No results found
			</Alert>
		);

	if (isHidden || _isEmpty(properties)) return <></>;

	return (
		<TableContainer className={classes.container}>
			<Table stickyHeader aria-label='sticky table'>
				<TableHead>
					<TableRow>
						{columns.map(column => (
							<TableCell sx={{ py: 3 }} key={column.id} align={column.align}>
								{column.label}
							</TableCell>
						))}
					</TableRow>
				</TableHead>
				<TableBody>
					{properties.map((row: PropertyData) => {
						const address = `${row.StreetAddress}, ${row.City}, ${row.State} ${row.Zip}`;
						return (
							<TableRow
								sx={{ py: 3 }}
								hover
								role='checkbox'
								tabIndex={-1}
								key={row.PropertyId}
							>
								<TableCell>{address}</TableCell>
								<TableCell>{row.Owner}</TableCell>
								<TableCell>
									<Button variant='text' onClick={() => onSelectHandler(row)}>
										Select
									</Button>
								</TableCell>
							</TableRow>
						);
					})}
				</TableBody>
			</Table>
		</TableContainer>
	);
};

export default SearchPropertyList;
