import { Grid, TextField } from '@material-ui/core';
import { useFormikContext } from 'formik';
import { ChangeEvent } from 'react';
import { TEditProperty } from '../../../../types';
import InputLabel from '../../../../ui-component/extended/Form/InputLabel';
import NumberFormatCustom from '../../components/NumberFormatCustom';

const DEFAULT_PRICE_DIFF = 1000;
const FIELDS = {
	min: 'priceMin',
	max: 'priceMax',
};

type Props = {
	disabled: boolean;
};

export const ListPrice = ({ disabled }: Props) => {
	const { values, handleBlur, touched, errors, setFieldValue } =
		useFormikContext<TEditProperty>();

	const handleChangePrice = (
		ev: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
	) => {
		const {
			target: { value, name },
		} = ev;
		const { priceMin, priceMax } = values;

		let newValue = +value;
		if (name === FIELDS.min && newValue >= priceMax) {
			newValue = priceMax - DEFAULT_PRICE_DIFF;
		}

		if (name === FIELDS.max && newValue <= priceMin) {
			newValue = priceMin + DEFAULT_PRICE_DIFF;
		}

		setFieldValue(name, newValue);
	};

	return (
		<>
			<Grid item lg={6} xs={12}>
				<InputLabel>List Price Min</InputLabel>
				<TextField
					value={values.priceMin}
					fullWidth
					placeholder='List Price Min'
					onChange={handleChangePrice}
					onBlur={handleBlur}
					error={touched.priceMin && Boolean(errors.priceMin)}
					helperText={touched.priceMin && errors.priceMin}
					name={FIELDS.min}
					InputProps={{
						inputComponent: NumberFormatCustom as any,
					}}
					disabled={disabled}
				/>
			</Grid>
			<Grid item lg={6} xs={12}>
				<InputLabel>List Price Max</InputLabel>
				<TextField
					value={values.priceMax}
					fullWidth
					placeholder='List Price Max'
					onChange={handleChangePrice}
					onBlur={handleBlur}
					error={touched.priceMax && Boolean(errors.priceMax)}
					helperText={touched.priceMax && errors.priceMax}
					name={FIELDS.max}
					InputProps={{
						inputComponent: NumberFormatCustom as any,
					}}
					disabled={disabled}
				/>
			</Grid>
		</>
	);
};
