import { Grid, Table, TableBody, TableContainer } from '@material-ui/core';
import { formatPhoneNumberIntl } from 'react-phone-number-input';
import _get from 'lodash.get'
import SubCard from '../../../../ui-component/cards/SubCard';
import TextRow from '../../components/TextRow';
import { TAddProperty } from '../../../../types';

type Props = {
	sellerInfo: TAddProperty;
};

const SellerInfo = ({ sellerInfo }: Props) => (
	<Grid item>
		<SubCard title='Seller Information'>
			<Grid item>
				<TableContainer>
					<Table
						sx={{
							'& td': {
								borderBottom: 'none',
							},
						}}
						size='small'
					>
						<TableBody>
							<TextRow<TAddProperty> data={sellerInfo} source='Email' label='Email' />
							<TextRow<TAddProperty>
								data={sellerInfo}
								source='FirstName'
								label='First Name'
							/>
							<TextRow<TAddProperty>
								data={sellerInfo}
								source='LastName'
								label='Last Name'
							/>
							<TextRow<TAddProperty>
								data={sellerInfo}
								label='Phone'
								getValue={(info: TAddProperty) =>
									formatPhoneNumberIntl(_get(info, 'Phone') || '')
								}
							/>
						</TableBody>
					</Table>
				</TableContainer>
			</Grid>
		</SubCard>
	</Grid>
);

export default SellerInfo;
