import { TableContainer, Stepper, Step, StepLabel } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { useQuery, gql } from '@apollo/client';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import SubCard from '../../../../ui-component/cards/SubCard';
import { listPropertyStatuses } from '../../../../graphql/queries';
import { TPropertyStatus } from '../../../../types';
import Loader from '../../../../ui-component/Loader';

const STEP_LIMIT = 3;

type Props = {
	fullAddress: string;
	status: number;
};

const useStyles = makeStyles(theme => ({
	stepper: {
		padding: 0,
	},
	icon: {
		color: theme.palette.grey[500],
		'&$activeIcon': {
			color: '#5BB573',
		},
		'&$completedIcon': {
			color: '#5BB573',
		},
	},
	activeIcon: {},
	completedIcon: {},
}));

const Status = ({ fullAddress, status }: Props) => {
	const classes = useStyles();
	const { loading, error, data } = useQuery(gql(listPropertyStatuses));

	if (loading) {
		return <Loader />;
	}

	if (loading || error || _isEmpty(data)) {
		return <></>;
	}

	const steps: Array<string> = _get(data, 'listPropertyStatuses', [])
		.slice(0, STEP_LIMIT)
		.map((status: TPropertyStatus) => _get(status, 'Name'));
	return (
		<SubCard title='Home Sale Status'>
			<TableContainer>
				<p>
					Current status of your property <strong>{fullAddress}</strong>
				</p>
				<Stepper
					className={classes.stepper}
					activeStep={status}
					orientation='vertical'
				>
					{steps.map(label => (
						<Step key={label}>
							<StepLabel
								StepIconProps={{
									classes: {
										root: classes.icon,
										active: classes.activeIcon,
										completed: classes.completedIcon,
									},
								}}
							>
								{label}
							</StepLabel>
						</Step>
					))}
				</Stepper>
			</TableContainer>
		</SubCard>
	);
};

export default Status;
