import { Grid, useTheme, Alert } from '@material-ui/core';
import { useEffect } from 'react';
import _get from 'lodash.get';
import { useMutation, gql, useQuery } from '@apollo/client';
import _isEmpty from 'lodash.isempty';
import { gridSpacing } from '../../../store/constant';
import { PropertyData } from '../../../types';
import { selectDataTreeProperty } from '../../../graphql/mutations';
import graphqlSchema from '../../../graphql';
import Loader from '../../../ui-component/Loader';
import MainCard from '../../../ui-component/cards/MainCard';
import ApproveActionButton from './components/ApproveAction';
import { useSnackBar } from '../../../hooks';
import Form from './components/Form';
import { shouldStopPolling } from './Helper';

type Props = {
	id: string;
};

const PULLING_SECOND = 7000;

const PropertyEdit = ({ id }: Props) => {
	const showSnackBar = useSnackBar();
	const theme = useTheme();

	const { loading, error, data, refetch, startPolling, stopPolling } = useQuery(
		gql(graphqlSchema.getProperty),
		{
			variables: { PropertyId: id },
		}
	);

	const {
		loading: isLoadingVestingTypes,
		error: errorVestingTypes,
		data: vestingTypesData,
	} = useQuery(gql(graphqlSchema.listSupportedVestingTypes));
	const [selectDataTreePropertyAction] = useMutation(
		gql(selectDataTreeProperty)
	);

	const property = _get(data, 'getProperty', {});
	const propertyNo = _get(property, 'PropertyNo', '');
	const isStop = shouldStopPolling(property);

	const listSupportedVestingTypesOptions = _get(
		vestingTypesData,
		'listSupportedVestingTypes',
		[]
	);

	useEffect(() => {
		if (isStop) stopPolling();
	}, [isStop]);

	if (errorVestingTypes) {
		showSnackBar({
			message: 'Something went wrong',
		});
		return <></>;
	}

	if (error && _isEmpty(property)) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Property not found
			</Alert>
		);
	}

	const onSelectPropertyHandler = async (property: PropertyData) => {
		try {
			const { PropertyId } = property;
			await selectDataTreePropertyAction({
				variables: {
					PropertyId: id,
					DataTreePropertyId: PropertyId,
				},
			});
			refetch();
			startPolling(PULLING_SECOND);
		} catch (e) {
			const message = _get(e, 'message', 'Something went wrong');
			showSnackBar({ message });
		}
	};

	return (
		<MainCard
			title={propertyNo}
			content={false}
			secondary={<ApproveActionButton property={property} refetch={refetch} />}
		>
			{(loading || isLoadingVestingTypes) && <Loader />}
			<Grid container spacing={gridSpacing}>
				<Grid item xs={12}>
					{!_isEmpty(data) && (
						<Form
							onSelectProperty={onSelectPropertyHandler}
							vestingTypesData={listSupportedVestingTypesOptions}
							formData={data}
							refetch={refetch}
						/>
					)}
				</Grid>
			</Grid>
		</MainCard>
	);
};

export default PropertyEdit;
