import { Table, TableBody, TableContainer } from '@material-ui/core';
import _isEmpty from 'lodash.isempty';
import { TReportStatus } from '../../../../types';
import TextRow from '../../components/TextRow';

type Props = {
	taxReportStatus: TReportStatus;
};

const TaxReport = ({ taxReportStatus }: Props) => {
	if (_isEmpty(taxReportStatus)) return <></>;
	return (
		<TableContainer>
			<Table
				sx={{
					'& td': {
						borderBottom: 'none',
					},
				}}
				size='small'
			>
				<TableBody>
					<TextRow<TReportStatus>
						data={taxReportStatus}
						source='TaxReportTaxResourceStatus'
						label='Tax Resource Status'
					/>
					<TextRow<TReportStatus>
						data={taxReportStatus}
						source='TaxReportTaxResourceMessage'
						label='Tax Resource Message'
					/>
					<TextRow<TReportStatus>
						data={taxReportStatus}
						source='TaxReportTotalViewStatus'
						label='Total View Status'
					/>
					<TextRow<TReportStatus>
						data={taxReportStatus}
						source='TaxReportTotalViewMessage'
						label='Total View Message'
					/>
				</TableBody>
			</Table>
		</TableContainer>
	);
};

export default TaxReport;
