import { useFormikContext } from 'formik';
import { TextField } from '@material-ui/core';
import SubCard from '../../../../ui-component/cards/SubCard';
import { TEditProperty } from '../../../../types';
import InputLabel from '../../../../ui-component/extended/Form/InputLabel';

const CalendarUrl = () => {
	const { values, handleChange, handleBlur, touched, errors } =
		useFormikContext<TEditProperty>();

	return (
		<SubCard title='Calendar'>
			<InputLabel>Calendar URL</InputLabel>
			<TextField
				id='calendarUrl'
				value={values.calendarUrl}
				fullWidth
				placeholder='Calendar URL'
				onChange={handleChange}
				onBlur={handleBlur}
				error={touched.calendarUrl && Boolean(errors.calendarUrl)}
				helperText={touched.calendarUrl && errors.calendarUrl}
				name='calendarUrl'
			/>
		</SubCard>
	);
};

export default CalendarUrl;
